.add-services {
  position: relative;

  &__title {
    font-size: 60px;
    font-family: 'MullerExtraBold', serif;
    letter-spacing: 1.5px;
    margin-top: rem(80);
  }

  &__subtitle {
    font-size: 24px;
    font-family: 'MullerRegular', serif;
    letter-spacing: 2.4px;
    text-transform: uppercase;
    margin-bottom: rem(80);
  }

  &__wrap {
    display: flex;
    flex-wrap: wrap;
  }

  &__arrow {
    display: inline-flex;
    height: rem(8);
    width: rem(12);
    background: url("../img/add-services/arrow-right-w.png") no-repeat center;
    background-size: contain;
  }

  &__img {
    position: absolute;
    right: 0;
    bottom: rem(30);
    width: 60%;
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      transition: all 300ms cubic-bezier(0.250, 0.250, 0.750, 0.750);

    }
  }

  &__link {
    position: absolute;
    bottom: rem(60);
    left: rem(60);
    font-size: 16px;
    letter-spacing: 1.6px;
    color: #fff;
    transition: 200ms ease;
    &:hover {
      transition: 200ms ease;
      left: rem(70);
      color: #fff;
    }
  }

  &__item {
    position: relative;
    width: 33.3333%;
    height: 340px;
    padding: rem(60);
    background: #000;
    margin-bottom: 0;
    overflow: hidden;

    figcaption {
      position: absolute;
      top: rem(60);
      color: #ffffff;
      font-size: 30px;
      font-family: "MullerLight", serif;
      letter-spacing: 0.75px;
      line-height: 40px;

      span {
        font-family: "MullerExtraBold", serif;
      }
    }

    &:nth-of-type(even) {
      background: #fff;

      figcaption {
        color: #000;
      }

      .add-services__link {
        color: #000;
      }
      .add-services__arrow{
        background-image: url("../img/add-services/arrow-right-b.png");
      }
    }

    &:hover {
      .add-services__img img {
        transform: scale(1.1);
        transition: all 300ms cubic-bezier(0.250, 0.250, 0.750, 0.750);
      }
    }
  }
}