.reviews {
  position: relative;
  &__title {
    font-size: 60px;
    font-family: 'MullerExtraBold', serif;
    letter-spacing: 1.5px;
  }

  &__subtitle {
    font-size: 24px;
    font-family: 'MullerRegular', serif;
    letter-spacing: 2.4px;
    text-transform: uppercase;
  }

  &__wrap {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    max-width: 31.33%;
    margin-top: rem(150);
    padding: rem(100) rem(40) rem(40) rem(40);
    text-align: center;
    box-shadow: 0 0 95px rgba(60, 60, 60, 0.12);
    z-index: 1;
    background: #fff;
  }

  &__photo {
    position: absolute;
    top: rem(-82.5);
    height: rem(165);
    width: rem(165);

    img {
      border-radius: 100%;
      overflow: hidden;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    &:before {
      content: '';
      position: absolute;
      height: rem(45);
      width: rem(45);
      right: 0;
      border-radius: 50%;
      background: url('../img/icons/quotes.png') no-repeat center;
      background-size: contain;
    }
  }

  &__name {
    color: #262626;
    font-family: "MullerExtraBold", serif;
    font-size: 24px;
    font-weight: 700;
    letter-spacing: 0.6px;
  }

  &__position {
    font-size: 14px;
    font-weight: 400;
    letter-spacing: 0.7px;
    line-height: 24px;
  }

  &__desk {
    font-family: "MullerLight", serif;
    font-weight: 300;
    letter-spacing: 0.8px;
    line-height: 24px;
    font-size: rem(14);
    margin-top: rem(40);
  }

  &__dots {
    position: absolute;
    left: 0;
    top: 55.4%;
    height: 275px;
    width: 540px;
    background: url('../img/dots.png') center center;
    background-size: 100%;
    transform: translateY(-50%);
    opacity: .23;
  }

  .right {
    right: 0%;
    left: inherit;
  }
  &__btn{
    display: flex;
    margin: rem(60) auto;
    padding: rem(30) rem(60);
    font-size: rem(18);
    font-family: 'MullerExtraBold', serif;
    color: var(--yellow);
    border-radius: rem(38);
    box-shadow: -13px -8px 43px rgba(9, 9, 9, 0.2);
    line-height: 19.2px;
    text-transform: uppercase;
    letter-spacing: 1.35px;
    border: none;
    background: #1c1a1c;
  }
}